/* Navigation Menu */

.nav {
	z-index: 10;
	display: grid;
	grid-gap: 30px;
	grid-auto-flow: column;
	grid-template-columns: repeat(4, minmax(75px, auto));
	text-align: center;

	@media (max-width: 767px) {
		z-index: 99;
		position: fixed;
		top: -100%;
		left: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(
			115.98deg,
			#5b52ed 0.66%,
			#953fe2 33.64%,
			#b15cfd 71.86%,
			#5b52ed 97.21%
		);
		opacity: 0;

		display: none;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		grid-gap: 0;

		transition: opacity 0.2s ease-in, top 0s linear 0.2s;

		> * + * {
			margin-top: 70px;
		}
	}
}

.nav__link {
	font-weight: 500;
	font-size: 14px;
	letter-spacing: 0.02em;
	color: $white;
	text-decoration: none;

	transition: 0.3s linear;
	outline: 0;

	&:hover {
		font-weight: 700;
	}

	&:focus {
		font-weight: 700;
		text-shadow: 0px 5px 15px #c1b9ee;
	}

	@media (max-width: 767px) {
		width: auto;
		font-weight: 500;
		font-size: 18px;
	}
}

.nav.nav--active {
	z-index: 99;
	display: flex;
	top: 0;
	opacity: 1;
	transition: top 0s linear, opacity 0.2s ease-in;
}
