/* Reset and base styles  */
*,
*::before,
*::after {
	box-sizing: border-box;
}

html {
	height: 100%;
	scroll-behavior: smooth;
}

body {
	height: 100%;
	display: flex;
	flex-direction: column;
	margin: 0;
	font-family: "Ubuntu", sans-serif;
	color: $black;
	overflow-x: hidden;
}

body > * {
	flex-shrink: 0;
}

input,
textarea,
button {
	font-family: inherit;
	padding: 0;
}

label {
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

img {
	display: block;
	max-width: 100%;
	height: auto;
}

ul {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

fieldset {
	border: none;
	margin: 0;
	padding: 0;
}

legend {
	display: block;
	margin: 0;
	padding: 0;
	border: none;
}

/* Grid styles */

.container {
	margin: 0 auto;
	width: 1440px;
	margin: 0 auto;
	padding-left: 150px;
	padding-right: 150px;

	@media (max-width: 1439px) {
		width: 1000px;
		padding-left: 50px;
		padding-right: 50px;
	}

	@media (max-width: 999px) {
		width: 100%;
	}

	@media (max-width: 873px) {
		padding-left: 50px;
		padding-right: 50px;
	}

	@media (max-width: 767px) {
		padding-left: 10px;
		padding-right: 10px;
	}
}

/* Utility */

.none {
	display: none !important;
}

.footer {
	margin-top: auto;
}

/* Section */

.section-title {
	font-weight: 700;
	font-size: 36px;
}

.section-text {
	font-weight: 400;
	font-size: 22px;
	line-height: 1.79;

	@media (max-width: 873px) {
		font-size: 20px;
	}
}

.section-text + .section-text {
	margin-top: 4px;
}
