/* Header */

.header {
	position: relative;
	padding-top: 60px;
	padding-bottom: 99px;

	background-color: $brand-color;
	background: linear-gradient(
		115.98deg,
		#5b52ed 0.66%,
		#953fe2 33.64%,
		#b15cfd 71.86%,
		#5b52ed 97.21%
	);

	color: $white;
	overflow: hidden;

	&::after {
		position: absolute;
		content: "";
		display: block;
		width: 1440px;
		height: 180px;

		bottom: 0;
		right: 0;

		background: linear-gradient(
			135deg,
			transparent 9.5%,
			rgb(255, 255, 255) 9.5%
		);

		@media (max-width: 1439px) {
			display: none;
		}
	}

	@media (max-width: 1439px) {
		padding-top: 30px;
		padding-bottom: 154px;
	}

	@media (max-width: 873px) {
		padding-bottom: 207px;
	}

	@media (max-width: 767px) {
		padding-top: 7px;
		padding-bottom: 200px;
	}

	@media (max-width: 380px) {
		padding-bottom: 57px;
	}
}

.header__top {
	position: relative;
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 133px;

	@media (max-width: 1439px) {
		margin-bottom: 113px;
	}

	@media (max-width: 873px) {
		margin-bottom: 110px;
	}

	@media (max-width: 767px) {
		margin-bottom: 66px;
		height: 50px;
	}
}

.logo {
	margin-left: -6px;
	width: 142px;
	height: 41px;
	fill: none;

	@media (max-width: 999px) {
		margin-left: 0;
	}

	@media (max-width: 767px) {
		display: none;
	}
}

.logo-small {
	display: none;
	width: 41px;
	height: 50px;

	@media (max-width: 767px) {
		z-index: 999;
		fill: none;
		display: block;
	}
}

.logo-small--fixed {
	@media (max-width: 767px) {
		z-index: 999;
		position: fixed;
	}
}

.header__title {
	margin-bottom: 24px;
	width: 628px;

	font-weight: 700;
	font-size: 46px;

	@media (max-width: 873px) {
		margin-bottom: 15px;
		width: 555px;
		font-size: 40px;
	}

	@media (max-width: 767px) {
		width: 350px;
		font-size: 36px;
	}

	@media (max-width: 380px) {
		width: 300px;
	}
}

.header__subtitle {
	margin-bottom: 105px;
	width: 400px;

	font-weight: 400;
	font-size: 22px;

	@media (max-width: 1439px) {
		margin-bottom: 57px;
	}

	@media (max-width: 873px) {
		margin-bottom: 50px;
		font-size: 20px;
	}

	@media (max-width: 767px) {
		width: 235px;
	}

	@media (max-width: 380px) {
		margin-bottom: 344px;
	}
}

.header__info {
	margin-bottom: 122px;

	display: grid;
	grid-template-columns: repeat(2, 222px);
	column-gap: 20px;
	row-gap: 31px;

	@media (max-width: 1439px) {
		margin-bottom: 51px;
	}

	@media (max-width: 873px) {
		margin-bottom: 47px;
		width: 440px;
		column-gap: 10px;
		row-gap: 20px;
	}

	@media (max-width: 767px) {
		width: auto;
		grid-template-columns: repeat(2, 150px);
		column-gap: 43px;
	}

	@media (max-width: 440px) {
		column-gap: 42px;
	}

	@media (max-width: 359px) {
		grid-template-columns: repeat(2, 130px);
		column-gap: 20px;
	}
}

.header__item {
	display: grid;
	grid-auto-flow: column;
	column-gap: 15px;
	justify-content: left;

	font-weight: 500;
	font-size: 12px;
	line-height: 15px;

	@media (max-width: 873px) {
		column-gap: 7px;
	}
}

.header__icon {
	stroke: #ffd61b;
	fill: none;
	width: 24px;
	height: 24px;

	@media (max-width: 873px) {
		svg {
			width: 21px;
			height: 21px;
		}
	}
}

.header__img {
	position: absolute;
	display: block;
	height: 756px;
	width: 1113px;

	background-image: url('../img/header/header.png');
	background-repeat: no-repeat;
	background-position: right;
	background-size: contain;

	bottom: -70px;
	right: 0;

	@media (max-width: 1439px) {
		bottom: -290px;
	}

	@media (max-width: 767px) {
		transform: scale(0.9);
		bottom: -250px;
		right: -55px;
	}

	@media (max-width: 699px) {
		bottom: -285px;
    	right: -112px;
		transform: scale(0.8);
	}

	@media (max-width: 550px) {
		bottom: -300px;
		right: -167px;
		transform: scale(0.7);
	}

	@media (max-width: 440px) {
		bottom: -310px;
		right: -223px;
		transform: scale(0.6);
	}
	
	@media (max-width: 380px) {
		background-image: url('../img/header/header-small.png');
		background-position: center;
		background-size: contain;
		bottom: 0;
		right: 0;
		left: 0;
		top: -80px;
		transform: scale(1);
		width: 100%;
		height: auto;
	}

}

.header__img {
	
}

.button-wrap {
	position: relative;
	z-index: 9;

	filter: drop-shadow(0px 4px 6px rgba(0, 0, 0, 0.3));
	transition: 0.3s linear;

	&:hover {
		filter: drop-shadow(0px 4px 6px rgba(0, 0, 0, 0.7));
	}

	@media (max-width: 1439px) {
		filter: drop-shadow(0px 3px 8px rgba(0, 0, 0, 0.05));
	}

	@media (max-width: 380px) {
		text-align: center;
	}
}

.header__button {
	padding: 18px 32px;
	display: inline-block;
	clip-path: polygon(7% 0, 100% 0, 100% 73%, 93% 100%, 0 100%, 0 30%);

	background: linear-gradient(0deg, #b15cfd 0%, #7355f2 100%);
	border-radius: 5px;
	border: none;

	font-family: "Ubuntu", sans-serif;
	font-weight: 700;
	font-size: 18px;
	color: $white;
	text-decoration: none;

	outline: 0;
	transition: 0.3s linear;
	cursor: pointer;

	@media (max-width: 1439px) {
		padding: 18px 33px;
		background: $white;
		color: #5c52ed;
	}

	@media (max-width: 873px) {
		padding: 15px 19px;
		clip-path: polygon(6% 0, 100% 0, 100% 73%, 93% 100%, 0 100%, 0 25%);
	}

	&:focus {
		background: $brand-color;

		@media (max-width: 1439px) {
			color: $white;
		}
	}
}
