// Ширина и высота линии в иконке
$lineWidth: 25px;
$lineHeight: 3px;
$color: #fff;

// Размеры контейнера
$width: $lineWidth;
$height: $lineHeight * 5;

.nav-icon {
    display: none;
	border: none;
	background-color: transparent;

    @media (max-width: 767px) {
        z-index: 99;
        width: $width;
        height: $height;

        display: flex;
        justify-content: center;
        align-items: center;

        cursor: pointer;
    }
}

.nav-icon__middle {
    position: relative;
    width: $lineWidth;
    height: $lineHeight;
    background-color: $color;
    border-radius: 2px;

    transition: background-color 0.2s linear 0.2s;

    &::before,
    &::after {
        content: "";
        position: absolute;
        left: 0;
        width: $lineWidth;
        height: $lineHeight;
        transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
        background-color: $color;
        border-radius: 2px;
    }

    &::before {
        top: -($lineHeight * 2);
    }

    &::after {
        top: $lineHeight * 2;
    }
}

/* Active state */

.nav-icon--active {
    position: fixed;
    top: 25px;
    right: 10px;
}

.nav-icon.nav-icon--active .nav-icon__middle {
    background-color: transparent;

    &::before,
    &::after {
        top: 0;
        transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
    }

    &::before {
        transform: rotate(45deg);
    }

    &::after {
        transform: rotate(-45deg);
    }
}
