/* Footer */
.footer {
	padding-top: 92px;
	padding-bottom: 55px;
	background: $footer;
	color: $contacts-color;

	@media (max-width: 1439px) {
		padding-top: 111px;
	}

	@media (max-width: 873px) {
		padding-bottom: 45px;
	}

	@media (max-width: 767px) {
		padding-top: 92px;
		padding-bottom: 55px;
	}
}

.footer__title {
	margin-bottom: 65px;
	color: $brand-color;

	@media (max-width: 1439px) {
		margin-bottom: 78px;
	}

	@media (max-width: 767px) {
		margin-bottom: 54px;
	}
}

.footer__content {
	margin-bottom: 67px;
	display: flex;
	justify-content: space-between;
	align-items: center;

	@media (max-width: 1439px) {
		display: block;
		margin-bottom: 50px;
	}

	@media (max-width: 767px) {
		margin-bottom: 74px;
	}
}

.footer__contacts {
	font-weight: 400;
	font-size: 24px;
	line-height: 1.79;

	a {
		margin-bottom: 12px;
		text-decoration: none;
		color: $contacts-color;

		transition: 0.3s linear;
		outline: 0;
		cursor: pointer;

		&:last-child {
			margin-bottom: 0;
		}

		&:hover,
		&:focus {
			color: $brand-color;
		}
	}

	@media (max-width: 1439px) {
		margin-bottom: 50px;
		display: grid;
		grid-template-columns: repeat(3, auto);
		justify-content: space-between;
		column-gap: 45px;
	}

	@media (max-width: 873px) {
		margin-bottom: 70px;
		font-size: 22px;
	}

	@media (max-width: 767px) {
		margin-bottom: 57px;
		grid-template-columns: repeat(2, auto);
		justify-content: flex-start;
	}

	@media (max-width: 450px) {
		display: block;
	}
}

.footer__phone {
	margin-top: 28px;
	margin-bottom: 30px;

	@media (max-width: 1439px) {
		margin-top: 0;
		margin-bottom: 0;
	}

	@media (max-width: 767px) {
		margin-bottom: 38px;
	}
}

.footer__address {
	margin-bottom: 46px;

	@media (max-width: 1439px) {
		margin-bottom: 0;
	}

	@media (max-width: 767px) {
		margin-bottom: 63px;
	}
}

.footer__social {
	display: grid;
	grid-auto-flow: column;
	column-gap: 18px;
	justify-content: flex-start;

	a {
		transition: 0.3s linear;
		outline: 0;

		&:hover,
		&:focus {
			fill: $brand-color;
		}
	}
}

.footer__social-icon {
	fill: $contacts-color;

	svg {
		width: 36px;
		height: 36px;
	}
}

.footer__map {
	width: 790px;
	height: 375px;

	@media (max-width: 1439px) {
		width: 100%;
		height: 324px;
	}

	@media (max-width: 873px) {
		height: 360px;
	}

	@media (max-width: 767px) {
		height: 340px;
	}

	iframe {
		border: 0px;
	}
}

.footer__copyright {
	display: flex;
	justify-content: space-between;

	font-weight: 400;
	font-size: 16px;
	line-height: 1.79;
}
