/* Section Call to Action */

.section-cta {
	padding-top: 170px;
	padding-bottom: 122px;

	@media (max-width: 1439px) {
		padding-top: 105px;
		padding-bottom: 145px;
	}

	@media (max-width: 873px) {
		padding-bottom: 110px;
	}

	@media (max-width: 767px) {
		padding-top: 70px;
		padding-bottom: 98px;
	}
}

.section-cta__content {
	margin-bottom: 131px;

	display: grid;
	grid-template-columns: 490px 450px;
	grid-template-rows: repeat(1, auto);
	justify-content: space-between;
	align-items: center;

	& > p + p {
		grid-column: 1/-1;
	}

	@media (max-width: 1439px) {
		margin-bottom: 95px;
		grid-template-columns: 330px 350px;
		column-gap: 94px;
	}

	@media (max-width: 873px) {
		margin-bottom: 75px;
		grid-template-columns: 305px 313px;
	}

	@media (max-width: 767px) {
		margin-bottom: 82px;
		display: block;
	}
}

.section-cta__title {
	margin-bottom: 43px;
	color: $brand-color;

	@media (max-width: 1439px) {
		margin-bottom: 55px;
	}
}

.section-cta__text {
	@media (max-width: 767px) {
		margin-bottom: 68px;
	}
}

.section-cta__info {
	margin-top: 17px;
	grid-column: 2/-1;
	grid-row: 1/2;
	filter: drop-shadow(4px 4px 15px rgba(0, 0, 0, 0.1));

	@media (max-width: 1439px) {
		margin-top: 0;
	}
}

.info {
	padding: 30px 30px;

	display: flex;
	align-items: center;
	justify-content: center;

	border-radius: 20px;
	background: linear-gradient(83.37deg, #fffdf5 2.57%, #f8f8f8 94.79%);
	clip-path: polygon(11% 0, 100% 0, 100% 77%, 89% 100%, 0 98%, 0 23%);

	@media (max-width: 1439px) {
		padding: 30px 19px;
		clip-path: polygon(11% 0, 100% 0, 100% 72%, 87% 100%, 0 100%, 0 20%);
	}

	@media (max-width: 873px) {
		padding: 25px 20px;
	}

	@media (max-width: 767px) {
		margin-top: 68px;
		padding: 29px 20px;
		margin: 0 auto;

		max-width: 340px;
	}
}

.info__img {
	margin-top: 17px;
	margin-right: 50px;

	@media (max-width: 1439px) {
		margin-right: 40px;
		margin-top: 11px;
		width: 78px;
	}

	@media (max-width: 450px) {
		margin-right: 10px;
	}
}

.info__text {
	font-weight: 400;
	font-size: 18px;
	line-height: 1.33;

	@media (max-width: 1439px) {
		font-size: 14px;
	}

	@media (max-width: 873px) {
		font-size: 12px;
	}

	@media (max-width: 767px) {
		width: 185px;
		font-size: 13px;
	}

	p {
		&:not(:last-child) {
			margin-bottom: 25px;

			@media (max-width: 1439px) {
				margin-bottom: 17px;
			}

			@media (max-width: 767px) {
				margin-bottom: 5px;
			}
		}
	}
}

.form {
	padding: 75px 82px;
	grid-column: 1/-1;

	display: grid;
	grid-template-columns: repeat(3, minmax(303px, auto));
	column-gap: 29px;
	row-gap: 30px;
	align-items: center;

	background: linear-gradient(
		101deg,
		#5a52ed -1.18%,
		#9865fe 24.38%,
		#8d6aff 58.05%,
		#b05cfd 99.81%
	);
	clip-path: polygon(0 0, 94% -2%, 100% 25%, 100% 100%, 5% 103%, 0 77%);

	@media (max-width: 1439px) {
		padding: 34px 60px;
		grid-template-columns: repeat(3, 204px);
		column-gap: 20px;
		justify-content: center;
	}

	@media (max-width: 873px) {
		padding: 40px 50px;
		grid-template-columns: repeat(3, 175px);
	}

	@media (max-width: 767px) {
		padding: 37px 0px;
		grid-template-columns: repeat(1, auto);
		row-gap: 20px;
		justify-items: center;

		clip-path: none;
	}
}

.form__label {
	&::placeholder {
		font-family: "Ubuntu", sans-serif;
		font-weight: 400;
		font-size: 18px;
		line-height: 1.79;
		color: #929292;
	}

	&:hover {
		box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.5);
	}
}

.form__input {
	padding: 24px 21px;
	width: 100%;

	background: #ffffff;
	border: 1px solid rgba(146, 146, 146, 0.3);

	font-family: "Ubuntu", sans-serif;
	font-size: 18px;

	outline: 0;
	transition: 0.3s linear;
	cursor: pointer;

	&:focus {
		border: 1px solid $brand-color;
		box-shadow: 0px 5px 15px #c1b9ee;
	}

	@media (max-width: 1439px) {
		padding: 17px 10px;
	}

	@media (max-width: 767px) {
		padding: 21px 10px;
		width: 320px;
	}

	@media (max-width: 359px) {
		width: 100%;
	}
}

.form__button {
	padding: 15px 5px;
	display: block;

	background: #ffffff;
	box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.25);
	border: 1px solid #fff;

	font-weight: 700;
	font-size: 18.6667px;
	color: #383838;

	outline: 0;
	transition: 0.3s linear;
	cursor: pointer;

	&:hover {
		box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.5);
	}

	&:focus {
		border: 1px solid $brand-color;
		box-shadow: 0px 5px 15px #c1b9ee;
	}

	@media (max-width: 767px) {
		margin-top: 25px;
		padding: 22px 95px;
	}

	@media (max-width: 359px) {
		padding: 22px 70px;
	}
}
