/* Section about */

.section-about {
	position: relative;
	padding-top: 72px;
	padding-bottom: 91px;

	&::after {
		position: absolute;
		content: "";
		display: block;
		width: 1440px;
		height: 160px;

		bottom: -160px;
		left: 0;

		background: linear-gradient(45deg, transparent 9%, rgb(255, 255, 255) 9%);

		@media (max-width: 1439px) {
			display: none;
		}
	}

	@media (max-width: 1439px) {
		padding-top: 131px;
		padding-bottom: 148px;
	}

	@media (max-width: 873px) {
		padding-top: 110px;
		padding-bottom: 212px;
	}

	@media (max-width: 767px) {
		padding-top: 85px;
		padding-bottom: 99px;
	}
}

.section-about__content {
	display: grid;
	grid-template-rows: repeat(3, auto);
	grid-template-columns: 383px 324px;
	column-gap: 103px;

	@media (max-width: 1439px) {
		margin-bottom: 71px;
		column-gap: 48px;
		grid-template-columns: repeat(2, auto);
	}

	@media (max-width: 767px) {
		margin-bottom: 57px;
		display: block;
	}
}

.section-about__title {
	margin-bottom: 54px;
	color: $brand-color;

	@media (max-width: 767px) {
		margin-bottom: 33px;
	}
}

.section-about__text {
	max-width: 510px;

	@media (max-width: 1439px) {
		max-width: 383px;
	}

	@media (max-width: 873px) {
		max-width: 345px;
	}

	@media (max-width: 767px) {
		max-width: 100%;
	}
}

.section-about__subtitle {
	margin-bottom: 73px;
	width: 466px;

	font-weight: 500;
	font-size: 26px;
	line-height: 1.36;
	color: $blue;

	@media (max-width: 873px) {
		margin-top: 90px;
	}

	@media (max-width: 820px) {
		width: 420px;
	}

	@media (max-width: 767px) {
		margin-top: 0;
		margin-bottom: 52px;
		width: auto;
	}
}

.section-about__img {
	margin-top: -110px;
	grid-column: 2/3;
	grid-row: 1/-1;

	@media (max-width: 1439px) {
		margin-top: -22px;
		width: 336px;
	}

	@media (max-width: 873px) {
		width: 280px;
	}

	@media (max-width: 767px) {
		margin: 0 auto;
		margin-top: 25px;
		width: 290px;
	}
}

.section-about__list {
	display: grid;
	grid-template-columns: repeat(3, 340px);
	column-gap: 58px;
	row-gap: 67px;

	@media (max-width: 1439px) {
		grid-template-rows: repeat(3, auto);
		grid-template-columns: repeat(2, 340px);
		column-gap: 57px;
		row-gap: 66px;
	}

	@media (max-width: 873px) {
		column-gap: 50px;
	}

	@media (max-width: 810px) {
		grid-template-columns: repeat(2, 320px);
	}

	@media (max-width: 767px) {
		display: block;

		& > * + * {
			margin-top: 63px;
		}
	}
}

.section-about__list-item {
	display: grid;
	grid-auto-flow: column;

	column-gap: 25px;
	align-items: center;
	justify-content: flex-start;

	@media (max-width: 1439px) {
		column-gap: 22px;
	}

	&--grid-position {
		@media (max-width: 1439px) {
			grid-row: 3/4;
		}
	}
}

.section-about__icon {
	width: 48px;
	height: 48px;

	stroke: $blue;
	overflow: visible;
}

.section-about__info {
	font-weight: 400;
	font-size: 22px;
}
