/* Section decision */

.section-decision {
	padding-top: 300px;
	padding-bottom: 110px;

	background: radial-gradient(
		132.47% 101.93% at 10.21% 70.31%,
		#b75cfe 0%,
		#4950ea 100%
	);
	color: $white;

	@media (max-width: 1439px) {
		padding-top: 116px;
		padding-bottom: 49px;
	}

	@media (max-width: 767px) {
		padding-top: 73px;
		padding-bottom: 75px;
	}
}

.section-decision__content {
	margin-bottom: 17px;

	display: grid;
	grid-template-columns: minmax(auto, 601px) minmax(auto, 423px);
	grid-template-rows: minmax(41px, auto) auto;
	column-gap: 56px;

	@media (max-width: 1439px) {
		margin-bottom: 115px;
		grid-template-columns: minmax(auto, 424px) minmax(auto, 331px);
		column-gap: 15px;
	}

	@media (max-width: 873px) {
		grid-template-columns: repeat(2, auto);
		margin-bottom: 100px;
		column-gap: 0;
	}

	@media (max-width: 767px) {
		margin-bottom: 58px;
		display: block;
	}

	& > p + p {
		grid-column: 1/-1;
	}
}

.section-decision__title {
	margin-bottom: 55px;
}

.section-decision__text {
	font-weight: 500;
}

.section-decision__img {
	margin-top: -64px;
	width: 425px;
	grid-column: 2/3;
	grid-row: 1/-1;

	@media (max-width: 1439px) {
		margin-top: 0;
		width: 330px;
	}

	@media (max-width: 873px) {
		width: 280px;
	}

	@media (max-width: 767px) {
		margin: 0 auto;
		margin-top: 30px;
		margin-left: 18px;
	}

	@media (max-width: 599px) {
		margin-left: 0;
	}
}

.section-decision__subtitle {
	margin-bottom: 67px;

	font-weight: 500;
	font-size: 26px;
	line-height: 1.36;

	@media (max-width: 767px) {
		margin-bottom: 56px;
	}
}

.section-decision__list {
	display: grid;
	grid-template-rows: repeat(2, auto);
	grid-template-columns: repeat (4, minmax(260px 320px));
	column-gap: 33px;
	row-gap: 56px;

	@media (max-width: 1439px) {
		grid-template-columns: repeat(2, 300px);
		grid-template-rows: repeat(4, auto);
		column-gap: 90px;
	}

	@media (max-width: 767px) {
		display: block;

		& > * + * {
			margin-top: 60px;
		}
	}
}

.section-decision__item {
	position: relative;
	padding-left: 30px;

	font-weight: 500;
	font-size: 20px;
	line-height: 1.2;

	&::before {
		content: "";
		display: block;
		width: 24px;
		height: 24px;

		position: absolute;
		left: -5px;
		top: 0;

		background-image: url(".././img/decision/check.svg");
	}

	&--grid-position {
		width: 420px;
		grid-column: 3/5;
		grid-row: 2/-1;

		@media (max-width: 1439px) {
			width: auto;
			grid-column: 1/2;
			grid-row: 4/-1;
		}

		@media (max-width: 767px) {
			grid-row: 7/-1;
		}
	}
}
